/* ========================================
// Silk Harmonica
// ===================================== */

/* When do accordions become harmonicas? */
$accordionToHarmonica: 1200px;

@define-mixin expanded-harmonica {

  .silk-harmonica--expanded & {

    @media(min-width: $accordionToHarmonica) {
      @mixin-content;
    }

  }

}

.silk-harmonica {
  margin-bottom: em(60);

  &--expanded {

    @media(min-width: $accordionToHarmonica) {
      display: flex;
      flex-wrap: wrap;
    }

  }

}

  .silk-harmonica__header {
    position: relative;
    margin-bottom: em(10);
    padding: em(13) em(30);
    cursor: pointer;
    background: $m2;
    border-radius: em(2);
    color: #fff;
    transition:
      padding .4s,
      background .2s,
      box-shadow .2s,
      color .2s;

    @mixin expanded-harmonica {
      order: 1;
      padding: em(23) em(25);
      background: none;
      border-radius: 0;
      color: #B8B9B1;
    }

    h3 {
      color: inherit;
    }

    &:hover {
      background: $m1;

      @mixin expanded-harmonica {
        background: none;
        box-shadow: inset 0 em(-10) 0 0 $m1;
        color: $m1;
      }

    }

    &.active {

      @mixin expanded-harmonica {
        background: none;
        box-shadow: inset 0 em(-10) 0 0 $m2;
        color: $m2;
      }

      + * {
        display: block;
      }

    }

    & > *:first-child {
      margin-bottom: 0;
    }

  }

    .silk-harmonica__bottle {
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: em(60);
      background: $m1;
      border-radius: 0 em(2) em(2) 0;

      &:first-of-type {

        .active & {
          display: none;
        }

      }

      &:last-child {
        display: none;

        .active & {
          display: block;
        }

      }

      @mixin expanded-harmonica {
        visibility: hidden;
      }

    }

      .silk-harmonica__bubble {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }

        .silk-harmonica__symbol {
          display: block;
        }

  .silk-harmonica__content {
    display: none;
    border: 1px solid #979797;
    border-top: 0;
    margin-top: em(-10);
    margin-bottom: em(10);
    padding: em(30);
    background: #fff;
    border-radius: 0 0 em(3) em(3);

    @mixin expanded-harmonica {
      order: 2;
      width: 100%;
      border: 0;
      border-top: 1px solid #979797;
      padding-right: 0;
      padding-left: 0;
      background: none;
    }

    &:after {
      @mixin clear;
    }

  }


/* ========================================
// Silk Harmonica Solutions
// ===================================== */

.silk-harmonica--solutions {

  .silk-harmonica__header {
    border: 1px solid #E3E3E3;
    margin-bottom: 0;
    padding: 0;
    text-align: center;
    background: #fff;
    border-radius: 0;

    @media(min-width: $accordionToHarmonica) {
      flex: 1 auto;
      border-bottom: 0;
      background: #fff;
      box-shadow: 0 em(6) 0 0 $m3;
    }

    &:after {
      position: absolute;
      top: 100%;
      left: 50%;
      border: em(10) solid transparent;
      border-top-color: $m3;
      margin-top: em(6);
      content: '';
      opacity: 0;
      visibility: hidden;
      transform: translate(-50%, 0);
      transition:
        opacity .2s,
        visibility .2s;
    }

    &.active {
      border-bottom: 0;
    }

    &.active,
    &:hover {
      background: #fff;
      box-shadow: 0 em(6) 0 0 $m3;

      @media(min-width: $accordionToHarmonica) {
        box-shadow: 0 em(6) 0 0 $m3;
      }

      &:after {
        opacity: 1;
        visibility: visible;
      }

    }

    h6 {
      margin-bottom: em(4, 12);
      padding-top: em(12, 12);
      font-size: em(12);
      color: #7D8073;
    }

    h5 {
      margin-bottom: 0;
      padding-bottom: rem(14);
      color: #464646;

      @mixin media $aldnoah {
        box-shadow:
          0 1px 0 0 #e3e3e3,
          0 2px 0 0 #fff,
          0 3px 0 0 #e3e3e3;
      }

    }

      span {
        margin-right: rem(5);
      }

      svg {
        fill: $m2;
      }

    figure {
      display: none;
      padding: em(30);

      @mixin media $aldnoah {
        display: block;
      }

    }

      img {
        margin: 0 auto;
      }

    .silk-harmonica__bottle {
      display: none;
    }

  }

  .silk-harmonica__content {
    border: 1px solid #e3e3e3;
    padding: em(40) em(30) em(30);
    margin-top: em(35);
    margin-bottom: em(35);
    background: #fff;
    border-radius: 0;

    @mixin media $medium {
      padding-top: em(50);
    }

    @media(min-width: $accordionToHarmonica) {
      padding-right: em(45);
      padding-left: em(45);
    }

  }

}

  .solution__footer {
    border-top: 1px solid #e3e3e3;
    margin: em(40) em(-30) em(-30);
    padding: em(30);

    @media(min-width: $accordionToHarmonica) {
      margin-right: em(-45);
      margin-left: em(-45);
      padding-right: em(45);
      padding-left: em(45);
    }

    h4 {
      position: relative;
      margin-bottom: 0;
      padding-left: em(30, 14);
      font-size: em(14);
      font-weight: 800;
      text-transform: uppercase;
    }

      a {
        display: block;
        color: #7D8073;

        &:hover {
          color: $m2;
        }

      }

      svg {
        position: absolute;
        top: em(4, 14);
        left: 0;
        fill: $m3;
      }

      em {
        font-style: normal;
        font-weight: inherit;
        color: $m3;
      }

  }
