/* ========================================
// Media
// ===================================== */

/* Figure
// ------------------------------------- */

figure {
  margin: 0;

  img {
    display: block;
    max-width: 100%;
  }

}

.shifted-asset {
  margin-bottom: em(30);

  @mixin media $wooser {
    max-width: em(410);
  }

  &--left {

    @mixin media $wooser {
      float: left;
      margin-right: em(40);
    }

  }

  &--right {

    @mixin media $wooser {
      float: right;
      margin-left: em(40);
    }

  }

  &--outlined {
    border: 1px solid #979797;
  }

  .blockquote-bubble & {

    .cssanimations .home & {

      @mixin media $gamagori {
        opacity: 0;
        transform: scale(.75);
      }

    }

    .home .element-halfway & {

      @mixin media $gamagori {
        animation: blockquote-bubble 1s forwards;
      }

    }

  }

  img {
    margin: 0 auto;
  }

}

@keyframes blockquote-bubble {

  100% {
    opacity: 1;
    transform: scale(1);
  }

}

  figcaption {
    @mixin h6;
    margin-top: em(10);
    font-style: italic;
  }

  .block-out {

    .cssanimations .home & {

      @mixin media $gamagori {
        opacity: 0;
        transform: scale(.95);
        transform-origin: left center;
      }

    }

    .home .element-halfway & {

      @mixin media $gamagori {
        animation: blockquote-bubble 1s .2s forwards;
      }

    }

  }


/* Image
// ------------------------------------- */

img {
  display: block;

  .hero > & {
    display: none;
  }

}

.full-width {
  width: 100%;
}

.titled-image {
  margin-bottom: em(35);
}


/* Iframe
// ------------------------------------- */

iframe {
  display: block;
  width: 100%;
}


/* SVG
// ------------------------------------- */

svg {}


/* Video
// ------------------------------------- */

video {}
